.popup {
  display: flex;
  width: 100vw;
  height: 100vh;

  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;

  background: rgba(0, 0, 0, .5);

  visibility: hidden;
  transition: opacity 500ms linear, visibility 0.5s linear ;
  opacity: 0;
}